document.addEventListener('DOMContentLoaded', function(){
    iniciarApp();
});
function iniciarApp() {
    navegacionFija();
    //scrollNav();
}
function navegacionFija() {
    //Seleccionar el contenedor del navbar
    const barra = document.querySelector('.header');
    //Que elemento activara el evento scroll
    const sobreProducto = document.querySelector('.sobre-productos');
    const body = document.querySelector('body');
    window.addEventListener('scroll', function() {
        console.log(sobreProducto.getBoundingClientRect());
        //El 0 son los px que se paso del elemento, se agrega .bottom para que cuando toque la parte superio del navegador se ejecute
        if( sobreProducto.getBoundingClientRect().bottom < 0  ) { //metodo para la ubicaciones de elementos
            barra.classList.add('fijo');
            body.classList.add('body-scroll');
        } else {
            barra.classList.remove('fijo');
            body.classList.remove('body-scroll');
        }
    });
}
function scrollNav() {
    const enlaces = document.querySelectorAll('.nav a');
    //El evento click al ser mas de un elemento a se debe usar foreach para iterar uno por uno
    enlaces.forEach( enlace => {
        enlace.addEventListener('click', function(e) {
            e.preventDefault();//Se usa pare evitar el comportamiento por defualt
            const seccionScroll = e.target.attributes.href.value;
            const seccion = document.querySelector(seccionScroll);
            seccion.scrollIntoView({ behavior: "smooth"});
        });
    });
}
const showHiddenInput = (inputOverlay, inputPass, inputIcon) =>{
    const overlay = document.getElementById(inputOverlay),
        input = document.getElementById(inputPass),
        iconEye = document.getElementById(inputIcon);
    iconEye.addEventListener('click', () =>{
        if(input.type === 'password'){
            input.type = 'text';
            //iconEye.classList.add('bx-show');
            iconEye.classList.add('fa-eye-slash');
        }else{
            input.type = 'password';
            //iconEye.classList.remove('bx-show');
            iconEye.classList.remove('fa-eye-slash');

        }
        overlay.classList.toggle('overlay-content');
    })
}
showHiddenInput('input-overlay', 'input-pass', 'input-icon');
